import React, { Component } from "react";
import { Route } from "react-router-dom";

import "./css/Main.css";

import { HomePage } from "./pages/HomePage.tsx";
import { ContentPage } from "./pages/ContentPage.tsx";
import { AuthorsPage } from "./pages/AuthorsPage.tsx";
import { AboutPage } from "./pages/AboutPage";
import { Menu } from "./menu/Menu";

export class Main extends Component {
  render() {
    return (
      <main>
        <Menu {...this.props} />
        <Route
          exact
          path={`/${this.props.root.location.home}`}
          render={(props) => <HomePage {...this.props.root} />}
        />
        <Route
          exact
          path={`/${this.props.root.location.content}`}
          render={(props) => <ContentPage {...this.props.root} />}
        />
        <Route
          exact
          path={`/${this.props.root.location.authors}*`}
          render={(props) => <AuthorsPage {...this.props.root} />}
        />
        <Route
          exact
          path={`/${this.props.root.location.about}`}
          render={(props) => <AboutPage {...this.props} />}
        />
      </main>
    );
  }
}
