import React, { Component } from "react";
import { ThemesItem } from "./template/ThemesItem";
import * as axios from "axios";

type subitem = {
  link: string,
  value: any,
  key?: number
}

export class ContentPage extends Component {
  public getThemes = (): void => {
    if (this.props["themes"].length === 0) {
      axios["get"](this.props["api"].themes_api).then((response: Array<object>) => {
        this.props["actionCreator"]("SET_THEMES", response["data"][0]);
      });
    }
  };

  protected createSubitems = (items: Array<object>): Array<subitem> => {
    return items.map((item: object, index: number): subitem => ({
      link: item["link"],
      value: item["value"],
      key: index,
    }));
  };

  protected createThemesItem = (themes: Array<object> | undefined) => {
    this.getThemes();

    if (themes === undefined) {
      return [];
    }

    return themes.map((theme: object, index: number): JSX.Element => {
      return (
        <ThemesItem
          {...{
            link: {
              link: theme["title"].link,
              value: theme["title"].value,
              key: index,
            },
            subitems: theme["items"],
          }}
        />
      );
    });
  };

  render = (): JSX.Element => {
    return (
      <React.StrictMode>
        <div className="main-container">
          <div className="main-item">
            <div className="tp-themes-block">
              <ul className="tp-themes-list" key={new Date().getTime()}>
                {this.createThemesItem(this.props["themes"][0])}
              </ul>
            </div>
          </div>
        </div>
      </React.StrictMode>
    );
  };
}
