import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// Components
import "./css/Header.css";

type searcherModeType = "COMPACT" | "FULLSIZE";

export class Header extends Component {
  toggleMode = (searcherMode: searcherModeType): boolean | void => {
    if (this.props["mobileScreenWidth"] >= document.body.clientWidth) {
      this.props["actionCreator"]("CHANGE_SEARCHER_VALUE", "");

      // Switch between toggle searcher modes
      switch (searcherMode) {
        case "COMPACT":
          this.props["actionCreator"]("SWITCH_SEARCHER_MODE", "FULLSIZE");
          return true;

        case "FULLSIZE":
          this.props["actionCreator"]("SWITCH_SEARCHER_MODE", "COMPACT");

          // Delete all markers
          this.search({
            search: this.props["search"],
            expression: "",
            items: this.props["searchItems"],
            fast: true, // Enable fast searcher
          });

          return true;
      }
    }
  }

  setSearcherMode = (searcherMode: searcherModeType) => {
    if (searcherMode === "COMPACT") {
      return "header-searcher";
    } else if (searcherMode === "FULLSIZE") {
      return "header-searcher searcher_active";
    } else {
      return "header-seracher";
    }
  };

  search(props: object) {
    // Search an expression in some html items
    this.props["actionCreator"]("SEARCH_EXPRESSION", {
      search: props["search"],
      expression: props["expression"],
      items: props["items"],
      fast: false,
    });
  }

  async searchAPI(newValue: string) {
    await this.props["actionCreator"]("CHANGE_SEARCHER_VALUE", newValue);

    // Search an expression in some html items
    this.search({
      search: this.props["search"],
      expression: this.props["searcherValue"],
      items: this.props["searchItems"],
    });
  }

  render() {
    return (
      <header className={"header"}>
        <div className={"header-container"}>
          <p className={"header-title"}>
            <a href="/">Dooboox</a>
          </p>
          <div
            className={this.setSearcherMode(this.props["searcherMode"])}
          >
            <input
              type="text"
              name="searcher"
              id={"search"}
              value={this.props["searcherValue"]}
              onChange={(e) => this.searchAPI(String(e.target.value))}
            />
            <div
              className={"search-icon_box"}
              onClick={() => {
                this.toggleMode(this.props["searcherMode"]);
              }}
            >
              <FontAwesomeIcon icon={faSearch} className={"search-icon"} />
            </div>
          </div>
        </div>
      </header>
    );
  }
}
