// Images
import fish from "../../img/fish.jpeg";
import nonePage from "../../img/shitpost.png";
import { actionCreator } from "../actionCreator";
import { syntaxHighlight } from "../../components/modules/SyntaxHighlight";

let initalState = {
  actionCreator,
  bodyScrollState: "UNLOCKED",
  mobileScreenWidth: 590,
  themes: [],

  currentPage: [],
  currentPageId: 1,
  nextPage: false,
  texts: {},
  pageSearcherValue: "",

  location: {
    home: "dooboox/",
    content: "dooboox/content",
    authors: "dooboox/authors",
    about: "dooboox/about",
  },

  api: {
    themes_api: "https://sha1om.github.io/dooboox_api/themes.json",
    pages_api: {
      page: `https://sha1om.github.io/dooboox_api/pages/page-1/index.json`
    }
  },

  images: {
    fish,
    nonePage,
    logo: "https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg",
  },

  minimap: {
    mode: "minimap", // or "minimap minimap_active"
    curtainHeight: document.body.clientHeight + "px",
    curtainTopOffset: "0%",
    buttonClassList: "minimapButton", // or minimapButton_active

    onScroll: () => {
      let winScroll =
        document.body.scrollTop || document.documentElement.scrollTop;
      let height =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      let scrolled = (winScroll / height) * 100;
      return scrolled + "%";
    },
  },

  syntaxHighlight: {
    highlight: (codeLine) => {
      return new syntaxHighlight(codeLine).highlight();
    },
  },
};

export const rootReducer = (state = initalState, action) => {
  switch (action.type) {
    case "SWITCH_BODY_SCROLL": {
      switch (action.payload) {
        case "LOCK": {
          document.getElementsByTagName("body")[0].classList.add("scroll-lock");
          return { ...state, bodyScrollState: "LOCKED" };
        }

        case "UNLOCK": {
          // yep-yep
          document
            .getElementsByTagName("body")[0]
            .classList.remove("scroll-lock");
          return { ...state, bodyScrollState: "UNLOCKED" };
        }
      }
    }

    case "SWITCH_MINIMAP_MODE": {
      return {
        ...state,
        minimap: {
          ...state.minimap,
          mode: action.payload.minimapClassList,
          buttonClassList: action.payload.buttonClassList,
        },
      };
    }

    case "SCROLL_MINIMAP": {
      return {
        ...state,
        minimap: { ...state.minimap, curtainTopOffset: action.payload },
      };
    }

    case "SET_THEMES": {
      return { ...state, themes: [ action.payload.items ]};
    }

    case "SET_CURRENT_PAGE": {
      return {
        ...state,
        currentPageId: action.payload.id, 
        nextPage: action.payload.next, 
        currentPage: [ ...action.payload.components] 
      };
    }

    case "ADD_NEW_TEXT": {
      let newTexts = { ...state.texts };
      newTexts[action.payload.name] = [...[action.payload.text]];

      return { ...state, texts: { ...newTexts } };
    }

    case "SET_PAGE_SEARCHER_VALUE": {
      return { ...state, pageSearcherValue: action.payload};
    }

    default: {
      return state;
    }
  }
};
